<template>
  <h1>FACTURACION</h1>
  <div v-if="getEditFactura">
    <EditFactura />
  </div>
  <div v-else>
      <FormFacturar />
      <TablaFacturas/>
  </div>
  

</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
    props:{
        id:{
            type: String
        }
    },
    components:{
        FormFacturar : defineAsyncComponent(()=> import('@/modules/rifa/components/facturar/FormFacturar.vue')),
        TablaFacturas : defineAsyncComponent(()=> import('@/modules/rifa/components/facturar/TablaFacturas.vue')),
        EditFactura : defineAsyncComponent(()=> import('@/modules/rifa/components/facturar/EditFactura.vue')),
    },
    computed:{
        ...mapGetters('facturar',['getEditFactura'])
    },
    methods:{
        ...mapMutations('facturar',['setIdNumero'])
    },
    created(){
        this.setIdNumero(this.id)
    }
}
</script>

